.component-ShowItem {
    text-align: left;
    padding: 10px;
}

.component-ShowItem__header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.component-ShowItem__header__date {
    font-size: 14px;
