    -o-transition: transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

.component-ShowItem__image img:hover {
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
}

.component-ShowItem__title {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: start;
        align-items: flex-start;
    -ms-flex-pack: start;
        justify-content: flex-start;
}

.component-ShowItem__title h2 {
    font-size: 1em;
}

.component-ShowItem__title .block {
    display: inline-block;
    width: 5px;
    height: 25px;
    margin-right: 10px;
    margin-top: 3px;
    background-color: red;
    -ms-flex: 0 0 auto;
        flex: 0 0 auto;
}

.component-ShowItem__describe {
    color: #909090;
    font-size: 12px;
    margin-top: 5px;
}




